import React from "react";
import { PostLink } from "./link";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { PostWrapper } from "./wrapper";
import { ImageWrapper } from "./image/wrapper";
import { PostInfoContainer } from "./container";
import { PostDate } from "./date";
import { PostTitle } from "./title";
import { PostTags } from "./tags";
import { ImageStyles } from "./image/styles";

export const RecentPost = ({ article }) => {
  const { id, slug, frontmatter } = article;
  const { hero_image, hero_image_alt, date, title, tags } = frontmatter;

  return (
    <PostWrapper key={id}>
      <PostLink to={`/${slug}`}>
        <ImageWrapper>
          <GatsbyImage
            image={getImage(hero_image)}
            alt={hero_image_alt}
            imgStyle={ImageStyles}
          />
        </ImageWrapper>
        <PostInfoContainer>
          <PostDate>{date}</PostDate>
          <PostTitle>{title}</PostTitle>
          <PostTags tags={tags} />
        </PostInfoContainer>
      </PostLink>
    </PostWrapper>
  );
};
